import React, { Component } from 'react';

import { paymentTypes } from 'data/paymentTypes';

import { createOrder } from './orderFunctions';

class OrderForm extends Component {

  submitForm = (event) => {
    event.preventDefault();

    const { name, phone, address, paymentType } = this.form;

    createOrder(name.value, phone.value, address.value, paymentType.value)
      .then(result => {
        this.props.onSuccess(result);
      });
  } 

  render() {
    return (
      <div className="order-process__confirmed">
        <form
          onSubmit={this.submitForm} 
          ref={orderForm => this.form = orderForm}
          action="#">
          <div className="order-process__delivery">
            <h3 className="h3">Кому и куда доставить?</h3>
            <div className="order-process__delivery-form">
              <label className="order-process__delivery-label">
                <span className="order-process__delivery-text">Имя</span>
                <input 
                  type="text"
                  name="name"
                  required
                  placeholder="Представьтесь, пожалуйста"
                  className="order-process__delivery-input" />
              </label>
              <label className="order-process__delivery-label">
                <span className="order-process__delivery-text">Телефон</span>
                <input 
                  type="tel"
                  name="phone"
                  required
                  placeholder="Номер в любом формате"
                  className="order-process__delivery-input" />
              </label>
              <label className="order-process__delivery-label">
                <span className="order-process__delivery-text">Адрес</span>
                <input 
                  type="text"
                  name="address"
                  required
                  placeholder="Ваша покупка будет доставлена по этому адресу"
                  className="order-process__delivery-input order-process__delivery-input_adress" />
              </label>
            </div>
            <p>Все поля обязательны для заполнения. Наш оператор свяжется с вами для уточнения деталей заказа.</p>
          </div>

          <div className="order-process__paid">
            <h3 className="h3">хотите оплатить онлайн или курьеру при получении?</h3>
            <div className="order-process__paid-form">
              {paymentTypes.map((type, index) => 
                <label key={type.value}
                  className="order-process__paid-label">
                  <input 
                    type="radio" 
                    name="paymentType"
                    required
                    value={type.value}
                    defaultChecked={index === 0}
                    className="order-process__paid-radio" />
                  <span className="order-process__paid-text">
                    {type.name}
                  </span>
                </label>
              )}
            </div>
          </div>

          <button 
            type="submit"
            className="order-process__form-submit order-process__form-submit_click">
            Подтвердить заказ
          </button>
        </form>
      </div>
    );
  }
}

export default OrderForm;



