import React, { Component } from 'react';
import { Route, Switch, BrowserRouter } from 'react-router-dom';

import appConfig from 'appConfig';
import Home from 'components/home/Home';
import CatalogWithData from 'components/catalog/CatalogWithData';
import Favorite from 'components/favorite/Favorite';
import ProductWithData from 'components/product/ProductWithData';
import Order from 'components/order/Order';

import 'css/normalize.css';
import 'css/font-awesome.min.css';

class App extends Component {
  render() {
    return (
      <BrowserRouter basename={appConfig.baseUrlSlug}>
        <Switch>
          <Route path="/" exact component={Home} />
          <Route path="/catalog" component={CatalogWithData} />
          <Route path="/favorite" component={Favorite} />
          <Route path="/product/:productId" component={ProductWithData} />
          <Route path="/order" component={Order} />

          <Route component={Home} />
        </Switch>
      </BrowserRouter>
    );
  }
}

export default App;



