import appConfig from 'appConfig';

export const getCategoryById = (categoryId) => {
  const params = {
    method: 'GET',
    headers: new Headers({
      'Content-Type': 'application/json'
    })
  };
  
  return fetch(`${appConfig.apiBaseUrl}/categories`, params)
    .then(response => response.json())
    .then(result => {
      if (!result.data) {
        return;
      }

      const category = result.data.find((category) => category.id === categoryId);
      
      if (category) {
        return category.title;
      } else {
        return;
      }
    });
}



