import appConfig from 'appConfig';

export const createOrder = (name, phone, address, paymentType) => {
  let cartId = '';
  if (localStorage.getItem('cartId')) {
    cartId = localStorage.getItem('cartId');
  } else {
    return;
  }

  const queryObject = {
    name,
    phone,
    address,
    paymentType,
    cart: cartId
  };

  const queryParams = {
    method: 'POST',
    headers: new Headers({
      'Content-Type': 'application/json'
    }),
    body: JSON.stringify(queryObject)
  };

  return fetch(`${appConfig.apiBaseUrl}/order`, queryParams)
    .then(response => response.json())
    .then(result => {
      if (result.status === 'ok') {
        console.log('Заказ успешно оформлен');
        localStorage.removeItem('cartId');

        return result.data;
      } else {
        console.log(result);
      }
    });
}



