import appConfig from 'appConfig';

export const addToCart = (id, size, amount) => {
  let cartId = '';
  if (localStorage.getItem('cartId')) {
    cartId = localStorage.getItem('cartId');
  }

  const queryObject = {
    id,
    size,
    amount
  };

  const queryParams = {
    method: 'POST',
    headers: new Headers({
      'Content-Type': 'application/json'
    }),
    body: JSON.stringify(queryObject)
  };

  fetch(`${appConfig.apiBaseUrl}/cart/${cartId}`, queryParams)
    .then(response => response.json())
    .then(result => {
      if (result.data && result.data.id) {
        console.log('Товар успешно добавлен в Корзину');
        localStorage.setItem('cartId', result.data.id);
      }
    });
}

export const updateCart = (productId, productSize, productAmount) => {
  let cartId = '';
  if (localStorage.getItem('cartId')) {
    cartId = localStorage.getItem('cartId');
  }
  
  const queryObject = {
    id: productId,
    size: productSize,
    amount: productAmount
  };

  console.log(JSON.stringify(queryObject));
  const queryParams = {
    method: 'POST',
    headers: new Headers({
      'Content-Type': 'application/json'
    }),
    body: JSON.stringify(queryObject)
  };

  fetch(`${appConfig.apiBaseUrl}/cart/${cartId}`, queryParams)
    .then(response => response.json())
    .then(result => {
      if (result.data && result.data.id) {
        console.log('Товар успешно обновлён');

        if (!result.data.products || result.data.products.length === 0) {
          localStorage.removeItem('cartId');
        }
      }
    });
}



