import React, { Component } from 'react';

import { categories, colors, sizes, reasons, seasons, heelSizes, brands } from 'data/filters';

class CatalogSidebar extends Component {
  render() {
    return (
      <section className="sidebar">

        <section className="sidebar__division">
          <div className="sidebar__catalogue-list">
            <div className="sidebar__division-title">
              <h3>Каталог</h3><div className="opener-down"></div>
            </div>
            <ul>
              {categories.map((category, index) =>
                <li key={`${category}${index}`}>
                  <a href="">{category}</a>
                </li>
              )}
            </ul>
          </div>
        </section>

        <div className="separator-150 separator-150-1"></div>

        <section className="sidebar__division">
          <div className="sidebar__price">
            <div className="sidebar__division-title">
              <h3>Цена</h3><div className="opener-down"></div>
            </div>
            <div className="price-slider">
              <div className="circle-container">
                <div className="circle-1"></div>
                <div className="line-white"></div>
                <div className="line-colored"></div>
                <div className="circle-2"></div>
              </div>
              <div className="counter">
                <input type="text" className="input-1" defaultValue="1000" />
                <div className="input-separator"></div>
                <input type="text" className="input-2" defaultValue="30 000" />
              </div>
            </div>
          </div>
        </section>

        <div className="separator-150 separator-150-2"></div>

        <section className="sidebar__division">
          <div className="sidebar__color">
            <div className="sidebar__division-title">
              <h3>Цвет</h3><div className="opener-down"></div>
            </div>
            <ul>
              {colors.map((color, index) => 
                <li key={`${color.name}${index}`}>
                  <a href="">
                    <div className="color"
                      style={{backgroundColor:color.hex}}></div>
                    <span className="color-name">{color.name}</span>
                  </a>
                </li>
              )}
            </ul>
          </div>
        </section>

        <div className="separator-150 separator-150-3"></div>  
              
        <section className="sidebar__division">
          <div className="sidebar__size">
            <div className="sidebar__division-title">
              <h3>Размер</h3>
              <div className="opener-down"></div>
            </div>
            <ul>
              {sizes.map((size, index) => 
                <li key={`${size}${index}`}>
                  <label>
                    <input type="checkbox" className="checkbox" name={`checkbox-size-${size}`} />
                    <span className="checkbox-custom"></span>
                    <span className="label">{size}</span>
                  </label>
                </li>
              )}
            </ul>
          </div>
        </section>

        <div className="separator-150 separator-150-4"></div>   

        <section className="sidebar__division">    
          <div className="sidebar__heel-height">
            <div className="sidebar__division-title">
              <h3>Размер каблука</h3>
              <div className="opener-up"></div>
            </div>
            <ul>
              {heelSizes.map((heelSize, index) => 
                <li key={`${heelSize}${index}`}>
                  <label>
                    <input type="checkbox" className="checkbox" name={`checkbox-heelSize-${heelSize}`} />
                    <span className="checkbox-custom"></span>
                    <span className="label">{heelSize}</span>
                  </label>
                </li>
              )}
            </ul>
          </div>
        </section>

        <div className="separator-150 separator-150-5"></div>

        <section className="sidebar__division">  
          <div className="sidebar__occasion">
            <div className="sidebar__division-title">
              <h3>Повод</h3>
              <div className="opener-down"></div>
            </div>
            <ul>
              {reasons.map((reason, index) =>
                <li key={`${reason}${index}`}>
                  <a href="">{reason}</a>
                </li>
              )}
            </ul>
          </div>
        </section>

        <div className="separator-150 separator-150-6"></div>

        <section className="sidebar__division">
          <div className="sidebar__season">
            <div className="sidebar__division-title">
              <h3>Сезон</h3>
              <div className="opener-up"></div>
            </div>
            <ul>
              {seasons.map((season, index) =>
                <li key={`${season}${index}`}>
                  <a href="">{season}</a>
                </li>
              )}
            </ul>
          </div>
        </section>

        <div className="separator-150 separator-150-7"></div>

        <section className="sidebar__division">
          <div className="sidebar__brand">

            <div className="sidebar__division-title">
              <h3>Бренд</h3>
              <div className="opener-down"></div>
            </div>
            <ul>
              {brands.map((brand, index) =>
                <li key={`${brand}${index}`}>
                  <a href="">{brand}</a>
                </li>
              )}
            </ul>
          </div>

          <label>
            <input type="checkbox" className="checkbox" name="checkbox-disc" />
            <span className="checkbox-discount"></span> 
            <span className="text-discount">Со скидкой</span>
          </label>
          <div className="separator-240"></div>
        </section>
            
        <section className="sidebar__division">    
          <div className="drop-down">
            <a href=""><span className="drop-down-icon"></span>Сбросить</a>
          </div>
        </section>
      </section> 
    );
  }
}

export default CatalogSidebar;



