import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import { formatBigNumbers } from 'components/utils/formatters';
import { updateCart } from 'components/order/cartFunctions';

class OrderItem extends Component {

  decreaseAmount = () => {
    const item = this.props.item;
    const cartInfo = this.props.cartInfo;

    updateCart(item.id, cartInfo.size, cartInfo.amount - 1);
  }
  increaseAmount = () => {
    const item = this.props.item;
    const cartInfo = this.props.cartInfo;

    updateCart(item.id, cartInfo.size, cartInfo.amount + 1);
  }

  render() {
    const item = this.props.item;
    const cartInfo = this.props.cartInfo;

    return (
      <div className="basket-item">
        <div className="basket-item__pic">
          <img src={item.images[0]} alt={item.title} />
        </div>
        <div className="basket-item__product">
          <div className="basket-item__product-name">
            <Link
              to={`/product/${item.id}`} className="basket-item__product-name-link">
              {item.title}
            </Link>
          </div>
          <div className="basket-item__product-features">
            <div className="basket-item__size">
              Размер: <span>{cartInfo.size}</span>
            </div>
            <div className="basket-item__producer">
              Производитель: <span>{item.brand}</span>
            </div>
            <div className="basket-item__color">
              Цвет: <span>{item.color}</span>
            </div>
          </div>
        </div>
        <div className="basket-item__quantity">
          <div
            onClick={this.decreaseAmount} 
            className="basket-item__quantity-change basket-item-list__quantity-change_minus">-</div>
          {cartInfo.amount}
          <div
            onClick={this.increaseAmount} 
            className="basket-item__quantity-change basket-item-list__quantity-change_plus">+</div>
        </div>
        <div className="basket-item__price">
          {formatBigNumbers(item.price * cartInfo.amount)} 
          <i className="fa fa-rub" aria-hidden="true"></i>
        </div>
      </div>
    );
  }
}

export default OrderItem;



