export const checkIfFavorite = (productId) => {
  const favoriteArrayJSON = localStorage.getItem('favorite');
  if (!favoriteArrayJSON) {
    return false;
  }
  const favoriteArray = JSON.parse(favoriteArrayJSON);
  return favoriteArray.includes(productId);
}

export const addToFavorite = (productId) => {
  let favoriteArrayJSON = localStorage.getItem('favorite');
  let favoriteArray;
  if (!favoriteArrayJSON) {
    favoriteArray = [];
  } else {
    favoriteArray = JSON.parse(favoriteArrayJSON);
  }
  
  if (!favoriteArray.includes(productId)) {
    favoriteArray.push(productId);
  }
  favoriteArrayJSON = JSON.stringify(favoriteArray);
  localStorage.setItem('favorite', favoriteArrayJSON);
}

export const removeFromFavorite = (productId) => {
  let favoriteArrayJSON = localStorage.getItem('favorite');
  let favoriteArray;
  if (!favoriteArrayJSON) {
    return;
  } else {
    favoriteArray = JSON.parse(favoriteArrayJSON);
  }

  const productIndex = favoriteArray.indexOf(productId);
  if (productIndex > -1) {
    favoriteArray.splice(productIndex, 1);
  }
  favoriteArrayJSON = JSON.stringify(favoriteArray);
  localStorage.setItem('favorite', favoriteArrayJSON);
}



