import React, { Component } from 'react';
import appConfig from 'appConfig';

import Catalog from './Catalog';

class CatalogWithData extends Component {
  constructor(props) {
    super(props);
    this.state = {
      searchQuery: null,
      response: null
    };
  }

  componentWillMount() {
    this.fetchData();
  }

  componentWillUpdate(nextProps, nextState) {
    if (window.location.search !== this.state.searchQuery) {
      this.fetchData();
    }
  }

  fetchData = () => {
    const params = {
      method: 'GET',
      headers: new Headers({
        'Content-Type': 'application/json'
      })
    };

    this.setState({
      searchQuery: window.location.search
    });

    fetch(`${appConfig.apiBaseUrl}/products${window.location.search}`, params)
      .then(response => response.json())
      .then(result => {
        this.setState({
          response: result
        });
      });
  }

  render() {
    return (
      <Catalog {...this.state} {...this.props} data={this.state.response} />
    );
  }
};

export default CatalogWithData;



