import React, { Component } from 'react';

import sliderItem1 from 'img/product-card-pics/product-card__similar-products-slider-item-1.png';

class SimilarProductCard extends Component {
  render() {
    return (
      <div className="similar-products-slider__item-list__item-card item">
        <div className="similar-products-slider__item">
          <a href="product-card-desktop.html">
            <img src={sliderItem1} className="similar-products-slider__item-pic-1" alt="Ботинки женские" />
          </a>
        </div>
        <div className="similar-products-slider__item-desc">
          <h4 className="similar-products-slider__item-name">Ботинки женские</h4>
          <p className="similar-products-slider__item-producer">
            Производитель: <span className="producer">Norma J.Baker</span>
          </p>
          <p className="similar-products-slider__item-price">23 150</p>
        </div>    
      </div>
    );
  }
}

export default SimilarProductCard;



