import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import { formatBigNumbers } from 'components/utils/formatters';
import { updateCart } from 'components/order/cartFunctions';

class CartPanel extends Component {
  constructor(props) {
    super(props);

    this.state = {
      data: props.data,
      items: props.items
    };
  }

  componentWillReceiveProps(nextProps) {
		this.setState({
      data: nextProps.data,
      items: nextProps.items
    });  
  }

  removeFromCartWrapper = (productId) => (event) => {
    const productInCart = this.state.data.data.products.find(product => product.id === productId);

    updateCart(productId, productInCart.size, 0);
  }

  render() {
    const active = this.props.active;

    if (!this.state.data || this.state.data.status === 'error') {
      return (
        <div 
        className={"hidden-panel__basket basket-dropped " + (active ? 'hidden-panel__basket_visible' : '')}>
          <p className="basket-dropped__message">В корзине пока ничего нет. Не знаете, с чего начать? Посмотрите наши новинки!</p>
        </div>
      );
    }

    return (
      <div 
        className={"hidden-panel__basket basket-dropped " + (active ? 'hidden-panel__basket_visible' : '')}>
        <div className="basket-dropped__title">В вашей корзине:</div>
        <div className="basket-dropped__product-list product-list">

          {this.state.items.map((item) => 
            <div key={item.id} 
              className="product-list__item">
              <a
                href={item.images[0]}
                target="_blank" 
                className="product-list__pic">
                <img src={item.images[0]} alt={item.title} /> 
              </a>
              <Link to={`/product/${item.id}`}
                className="product-list__product">
                {item.title}
              </Link>
              <div className="product-list__fill"></div>
              <div className="product-list__price">
                {formatBigNumbers(item.price)}
                <i className="fa fa-rub" aria-hidden="true"></i>
              </div>
              <div
                onClick={this.removeFromCartWrapper(item.id)} 
                className="product-list__delete">
                <span className="visually-hidden">Удалить из Корзины</span>
                <i className="fa fa-times" aria-hidden="true"></i>
              </div>
            </div>
          )}
  
        </div>
        <Link
          to="/order" 
          className="basket-dropped__order-button">
          Оформить заказ
        </Link>
      </div>
    );
  }
}

export default CartPanel;



