import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class ProfilePanel extends Component {
  render() {
    const active = this.props.active;

    return (
      <div 
        className={"hidden-panel__profile " + (active ? 'hidden-panel__profile_visible' : '')}>
        <a className="hidden-panel__profile-link" href="">Личный кабинет</a>
        <Link className="hidden-panel__profile-link" to="/favorite">
          <i className="fa fa-heart-o" aria-hidden="true"></i>Избранное</Link>
        <a className="hidden-panel__profile-link" href="">Выйти</a>
      </div>
    );
  }
}

export default ProfilePanel;



