import React, { Component } from 'react';
import { parse } from 'qs';
import { Link } from 'react-router-dom';

import Header from 'components/shared/Header';
import Footer from 'components/shared/Footer';
import OverlookedSlider from 'components/shared/OverlookedSlider';
import { formatProductsAmount } from 'components/utils/formatters';
import { getCategoryById } from 'components/shared/categoriesFunctions';

import 'css/style.css';
import 'css/style-catalogue.css';
import CatalogSidebar from './CatalogSidebar';
import CatalogItemCard from './CatalogItemCard';

class Catalog extends Component {
  constructor(props) {
    super(props);

    this.state = {
      data: props.data,
      category: null
    };
  }

  componentWillUpdate() {
    const query = parse(window.location.search.substr(1));

    getCategoryById(parseInt(query.categoryId, 10))
      .then(result => {
        this.setState({
          category: result
        });
      });
  }

  componentWillReceiveProps(nextProps) {
		this.setState({
      data: nextProps.data
    });
  }

  render() {
    const query = parse(window.location.search.substr(1));

    return (
      <div className="container">
        <Header />

        <div className="site-path">
          <ul className="site-path__items">
            <li className="site-path__item">
              <Link 
                to="/"
                className="site-path__item-link">
                Главная
              </Link>
            </li>
            <li className="site-path__item">
              <Link 
                to={{
                  pathname: '/catalog'
                }}
                className="site-path__item-link">
                Каталог
              </Link>
            </li>
            {query.categoryId ?
              <li className="site-path__item">
                <Link 
                  to={{
                    pathname: '/catalog',
                    search: `?categoryId=${query.categoryId}`
                  }}
                  className="site-path__item-link">
                  {this.state.category}
                </Link>
              </li> : ''
            }
          </ul>
        </div>

        <main className="product-catalogue">

          <CatalogSidebar />

          <section className="product-catalogue-content">
            
            <section className="product-catalogue__head">
              <div className="product-catalogue__section-title">
                {this.state.category ? 
                  <h2 className="section-name">
                    {this.state.category}
                  </h2> :
                  <h2 className="section-name">Каталог</h2>
                }
                {this.state.data ? 
                  <span className="amount"> {formatProductsAmount(this.state.data.data.length)}</span> : ''}
              </div>
              <div className="product-catalogue__sort-by">
                <p className="sort-by">Сортировать</p>
                <select id="sorting">
                  <option value="">по популярности</option>
                  <option value="">по размеру</option>
                  <option value="">по производителю</option>
                </select>
              </div>
            </section>

            <section  className="product-catalogue__item-list">

              {this.state.data ? 
               this.state.data.data.map((item) => <CatalogItemCard data={item} key={item.id} /> ) : ''}

            </section>
            
            <div className="product-catalogue__pagination">
              <div className="page-nav-wrapper">
                <a className="angle-back">
                  <span className="visually-hidden">Предыдущая страница</span>
                </a>
                <ul className="catalogue-pagination__list">
                  <li className="active"><a href="">1</a></li>
                  <li><a href="">2</a></li>
                  <li><a href="">3</a></li>
                  <li><a href="">4</a></li>
                  <li><a href="">5</a></li>
                  <li><a href="">...</a></li>
                  <li><a href="">99</a></li>
                </ul>
                <a className="angle-forward">
                  <span className="visually-hidden">Следующая страница</span>
                </a>
              </div>
            </div>

          </section>
        </main>
          
        <OverlookedSlider />

        <Footer />
      </div>
    );
  }
}

export default Catalog;



