import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import headerLogo from 'img/header-logo.png';
import MainMenuWithData from './MainMenuWithData';
import ProfilePanel from './ProfilePanel';
import CartFetcher from './CartFetcher';

class Header extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isSearchPanelActive: false,
      isProfilePanelActive: false,
      isCartPanelActive: false
    };
  }

  toggleSearchPanel = () => {
    this.setState({
      isSearchPanelActive: !this.state.isSearchPanelActive
    });
  }

  toggleProfilePanel = () => {
    this.setState({
      isProfilePanelActive: !this.state.isProfilePanelActive
    });
  }

  toggleCartPanel = () => {
    this.setState({
      isCartPanelActive: !this.state.isCartPanelActive
    });
  }

  render() {
    return (
      <header className="header">
        <div className="top-menu">
          <div className="wrapper">
            <ul className="top-menu__items">
              <li className="top-menu__item">
                <a href="">Возврат</a>
              </li>
              <li className="top-menu__item">
                <a href="">Доставка и оплата</a>
              </li>
              <li className="top-menu__item">
                <a href="">О магазине</a>
              </li>
              <li className="top-menu__item">
                <a href="">Контакты</a>
              </li>
              <li className="top-menu__item">
                <a href="">Новости</a>
              </li>
            </ul>
          </div>
        </div>

        <div className="header-main">
          <div className="header-main__wrapper wrapper">
            <div className="header-main__phone">
              <a href="tel:+7-495-790-35-03">+7 495 79 03 5 03</a>
              <p>Ежедневно: с 09-00 до 21-00</p>
            </div>
            <div className="header-main__logo">
              <Link to="/">
                <h1>
                  <img src={headerLogo} alt="logotype" />
                </h1>
              </Link>
              <p>Обувь и аксессуары для всей семьи</p>
            </div>
            <div className="header-main__profile">
              <div className="header-main__pics">
                <div 
                  onClick={this.toggleSearchPanel}
                  className={"header-main__pic header-main__pic_search " + (this.state.isSearchPanelActive ? 'header-main__pic_search_is-hidden' : '')}></div>
                <div className="header-main__pic_border"></div>
                <div 
                  onClick={this.toggleProfilePanel} 
                  className="header-main__pic header-main__pic_profile">
                  <div className={"header-main__pic_profile_menu " + (this.state.isProfilePanelActive ? 'header-main__pic_profile_menu_is-active' : '')}></div>
                </div>
                <div className="header-main__pic_border"></div>
                <div
                  onClick={this.toggleCartPanel} 
                  className="header-main__pic header-main__pic_basket">
                  <div className="header-main__pic_basket_full">1</div>
                  <div 
                    className={"header-main__pic_basket_menu " + (this.state.isCartPanelActive ? 'header-main__pic_basket_menu_is-active' : '')}></div>
                </div>
              </div>

              <form action="#"
                className={"header-main__search " + (this.state.isSearchPanelActive ? 'header-main__search_active' : '')}>
                <input placeholder="Поиск" />
                <i className="fa fa-search" aria-hidden="true"></i>
              </form>
            </div>
          </div>

          <div 
            className={"header-main__hidden-panel hidden-panel " + (this.state.isProfilePanelActive || this.state.isCartPanelActive ? 'header-main__hidden-panel_visible' : '')}>
            <ProfilePanel active={this.state.isProfilePanelActive} />

            <CartFetcher 
              type="panel"
              active={this.state.isCartPanelActive} />
          </div>
        </div>

        <MainMenuWithData />
      </header>
    );
  }
}

export default Header;



