import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import { categories, reasons, seasons, brands } from 'data/filters';

class DroppedMenu extends Component {
  hideMenu = () => {
    this.props.hideMenu();
  }

  render() {
    const categoryId = this.props.categoryId;

    return (
      <div 
        className="dropped-menu dropped-menu_visible">
        <div className="wrapper">

          <div className="dropped-menu__lists dropped-menu__lists_women">
            <h3 className="dropped-menu__list-title">Повод:</h3>
            <ul className="dropped-menu__list">
              {reasons.map((reason, index) =>
                <li key={`$${reason}${index}`} className="dropped-menu__item">
                  <Link 
                    to={{
                      pathname: '/catalog',
                      search: `?categoryId=${categoryId}&reason=${reason}`
                    }}
                    onClick={this.hideMenu}
                    className="dropped-menu__item-link">{reason}</Link>
                </li>
              )}
            </ul>
          </div>

          <div className="dropped-menu__lists dropped-menu__lists_three-coloumns">
            <h3 className="dropped-menu__list-title">Категории:</h3>
            <ul className="dropped-menu__list">
              {categories.map((category, index) =>
                <li key={`${category}${index}`} className="dropped-menu__item">
                  <Link 
                    to={{
                      pathname: '/catalog',
                      search: `?categoryId=${categoryId}&type=${category}`
                    }}
                    onClick={this.hideMenu}
                    className="dropped-menu__item-link">{category}</Link>
                </li>
              )}
            </ul>
          </div>
          
          <div className="dropped-menu__lists">
            <h3 className="dropped-menu__list-title">Сезон:</h3>
            <ul className="dropped-menu__list">
              {seasons.map((season, index) =>
                <li key={`${season}${index}`} className="dropped-menu__item">
                  <Link 
                    to={{
                      pathname: '/catalog',
                      search: `?categoryId=${categoryId}&season=${season}`
                    }}
                    onClick={this.hideMenu}
                    className="dropped-menu__item-link">{season}</Link>
                </li>
              )}
            </ul>
          </div>

          <div className="dropped-menu__lists">
            <h3 className="dropped-menu__list-title">Бренды:</h3>
            <ul className="dropped-menu__list">
              {brands.slice(0,5).map((brand, index) => 
                <li key={`${brand}${index}`} className="dropped-menu__item">
                  <Link 
                    to={{
                      pathname: '/catalog',
                      search: `?categoryId=${categoryId}&brand=${brand}`
                    }}
                    onClick={this.hideMenu}
                    className="dropped-menu__item-link">{brand}</Link>
                </li>
              )}
              <li className="dropped-menu__item">
                <Link 
                  to={{
                    pathname: '/catalog',
                    search: `?categoryId=${categoryId}`
                  }}
                  onClick={this.hideMenu}
                  className="dropped-menu__item-link">Все</Link>
              </li>
            </ul>
          </div>

        </div>
      </div>
    );
  }
}

export default DroppedMenu;



