import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import { formatBigNumbers } from 'components/utils/formatters';
import { checkIfFavorite, addToFavorite, removeFromFavorite } from 'components/favorite/favoriteFunctions';

class CatalogItemCard extends Component {
  constructor(props) {
    super(props);

    this.state = {
      currentImageIndex: 0,
      isFavorite: null
    };
  }

  componentDidMount() {
    this.setState({
      isFavorite: checkIfFavorite(this.props.data.id)
    });
  }

  showPrevImage = () => {
    if (this.state.currentImageIndex > 0) {
      this.setState({
        currentImageIndex: this.state.currentImageIndex - 1
      });
    }
  }
  showNextImage = () => {
    if (this.state.currentImageIndex < this.props.data.images.length - 1) {
      this.setState({
        currentImageIndex: this.state.currentImageIndex + 1
      });
    }
  }

  toggleFavorite = (productId) => (event) => {
    if (this.state.isFavorite) {
      removeFromFavorite(productId);
    } else {
      addToFavorite(productId);
    }

    this.setState({
      isFavorite: checkIfFavorite(this.props.data.id)
    });
  }

  render() {
    const data = this.props.data;

    return (
      <section className="item-list__item-card item">
        <Link className="item__link" to={`/product/${data.id}`}>
          <div className="item-pic">
            <img 
              className="item-pic-1" 
              src={data.images[this.state.currentImageIndex]} 
              alt={data.title} />
          </div>
          <div className="item-desc">
            <h4 className="item-name">{data.title}</h4>
            <p className="item-producer">Производитель:&nbsp;
              <span className="producer">{data.brand}</span>
            </p>
            <p className="item-price">{formatBigNumbers(data.price)}</p>
            <div className="sizes">
              <p className="sizes__title">Размеры в наличии:</p>
              <p className="sizes__avalible">36, 37, 38, 39, 40, 41, 42</p>
            </div>
          </div>
        </Link>

        <div
          onClick={this.showPrevImage} 
          className="arrow arrow_left">
          <span className="visually-hidden">Предыдущее изображение</span>
        </div>
        <div
          onClick={this.showNextImage} 
          className="arrow arrow_right">
          <span className="visually-hidden">Следующее изображение</span>
        </div>
        <div 
          onClick={this.toggleFavorite(data.id)}
          className={'product-catalogue__product_favorite ' + 
          (this.state.isFavorite ? 'product-catalogue__product_favorite-chosen' : '')}>
          <p></p>
        </div>
      </section>
    );
  }
}

export default CatalogItemCard;



