import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import { formatBigNumbers } from 'components/utils/formatters';
import { checkIfFavorite, addToFavorite, removeFromFavorite } from 'components/favorite/favoriteFunctions';

import NewProductsMenuWithData from './NewProductsMenuWithData';

class NewProducts extends Component {
  constructor(props) {
    super(props);

    this.state = {
      data: props.data,
      currentItemIndex: 0,
      currentCategoryId: null,
      isCurrentItemFavorite: null
    };
  }

  componentWillReceiveProps(nextProps) {
		this.setState({
      data: nextProps.data
    });  
  }

  shouldComponentUpdate(nextProps, nextState) {
    return this.state.currentItemIndex !== nextState.currentItemIndex ||
      this.state.currentCategoryId !== nextState.currentCategoryId ||
      this.state.isCurrentItemFavorite !== nextState.isCurrentItemFavorite;
  }

  handleCategory = (id) => {
    this.setState({
      currentCategoryId: id
    });

    if (this.state.data && this.state.currentCategoryId && this.getCurrentSlide()) {
      this.setState({
        isCurrentItemFavorite: checkIfFavorite(this.getCurrentSlide().id)
      });
    }
  }

  getCurrentProductsArray() {
    return this.state.data.data.filter(item => item.categoryId === this.state.currentCategoryId);
  }

  getPreviousSlide() {
    if (this.state.data && this.state.currentCategoryId) {
      return this.getCurrentProductsArray()[this.state.currentItemIndex - 1];
    } else {
      return null;
    }
  }
  getCurrentSlide() {
    if (this.state.data && this.state.currentCategoryId) {
      return this.getCurrentProductsArray()[this.state.currentItemIndex];
    } else {
      return null;
    }
  }
  getNextSlide() {
    if (this.state.data && this.state.currentCategoryId) {
      return this.getCurrentProductsArray()[this.state.currentItemIndex + 1];
    } else {
      return null;
    }
  }

  goToPreviousSlide = () => {
    if (this.state.data &&
        this.state.currentCategoryId &&
        this.state.currentItemIndex > 0) {
      this.setState({
        currentItemIndex: this.state.currentItemIndex - 1
      });
    }
  }
  goToNextSlide = () => {
    if (this.state.data &&
        this.state.currentCategoryId &&
        this.state.currentItemIndex < this.getCurrentProductsArray().length - 1) {
      this.setState({
        currentItemIndex: this.state.currentItemIndex + 1
      });
    }
  }

  toggleFavorite = (productId) => (event) => {
    console.log(productId);
    console.log(this.state.isCurrentItemFavorite);

    if (this.state.isCurrentItemFavorite) {
      removeFromFavorite(productId);
    } else {
      addToFavorite(productId);
      console.log('added?');
    }

    this.setState({
      isCurrentItemFavorite: checkIfFavorite(this.getCurrentSlide().id)
    });
  }

  render() {
    // console.log(this.state.data, 'new data');
    
    return (
      <section className="new-deals wave-bottom">
        <h2 className="h2">Новинки</h2>

        <NewProductsMenuWithData onSelectCategory={this.handleCategory} />

        <div className="new-deals__slider">
          <div
            onClick={this.goToPreviousSlide} 
            className="new-deals__arrow new-deals__arrow_left arrow"></div>
          
          {(this.state.data && this.state.currentCategoryId && 
            this.getPreviousSlide()) ? 
            <div 
              className="new-deals__product new-deals__product_first"
              style={{backgroundImage: `url(${this.getPreviousSlide().images[0]})`}}>
              <Link
                to={`/product/${this.getPreviousSlide().id}`} 
                className="new-deals__product-link">
                <span className="visually-hidden">
                  {this.getPreviousSlide().title}
                </span>
              </Link>
            </div> : 
            <div 
              className="new-deals__product new-deals__product--inactive">
            </div>}
          
          {(this.state.data && this.state.currentCategoryId && 
            this.getCurrentSlide()) ?
            <div
              style={{backgroundImage: `url(${this.getCurrentSlide().images[0]})`}} 
              className="new-deals__product new-deals__product_active">
              <Link 
                to={`/product/${this.getCurrentSlide().id}`}
                className="new-deals__product-link">
                <span className="visually-hidden">
                  {this.getCurrentSlide().title}
                </span>
              </Link>
              <div
                onClick={this.toggleFavorite(this.getCurrentSlide().id)} 
                className={"new-deals__product_favorite " + (this.state.isCurrentItemFavorite ? 'new-deals__product_favorite--active' : '')}>
                {this.state.isCurrentItemFavorite ?
                  <span className="visually-hidden">Удалить из Избранного</span> : 
                  <span className="visually-hidden">Добавить в Избранное</span>
                }
              </div>
            </div> : ''}

          {(this.state.data && this.state.currentCategoryId && 
            this.getNextSlide()) ? 
            <div 
              className="new-deals__product new-deals__product_last"
              style={{backgroundImage: `url(${this.getNextSlide().images[0]})`}}>
              <Link
                to={`/product/${this.getNextSlide().id}`} 
                className="new-deals__product-link">
                <span className="visually-hidden">
                  {this.getNextSlide().title}
                </span>
              </Link>
            </div> : 
            <div 
              className="new-deals__product new-deals__product--inactive">
            </div>}

          <div
            onClick={this.goToNextSlide} 
            className="new-deals__arrow new-deals__arrow_right arrow"></div>
        </div>

        {(this.state.data && this.state.currentCategoryId && 
          this.getCurrentSlide()) ?
          <div className="new-deals__product-info">
            <Link 
              to={`/product/${this.getCurrentSlide().id}`}
              className="h3">
              {this.getCurrentSlide().title}
            </Link>
            <p>Производитель:
              <span>{this.getCurrentSlide().brand}</span>
            </p>
            <h3 className="h3">
              {formatBigNumbers(this.getCurrentSlide().price)} ₽
            </h3>
          </div> : ''}
      </section>
    );
  }
}

export default NewProducts;



