import React, { Component } from 'react';

import { formatBigNumbers } from 'components/utils/formatters';

import OrderItem from './OrderItem';

class Cart extends Component {
  constructor(props) {
    super(props);

    this.state = {
      data: props.data,
      items: props.items
    };
  }

  componentWillReceiveProps(nextProps) {
		this.setState({
      data: nextProps.data,
      items: nextProps.items
    });  
  }

  getProductCartInfo = (productId) => {
    const productInCart = this.state.data.data.products.find(product => product.id === productId);

    return productInCart;
  }

  getTotalSum = () => {
    let sum = 0;

    this.state.items.forEach((item) => {
      sum += item.price * this.getProductCartInfo(item.id).amount;
    });

    return sum;
  }

  render() {
    console.log(this.state.data, 'data');
    console.log(this.state.items, 'items');

    if (!this.state.data || this.state.data.status === 'error') {
      return (
        <div className="order-process__basket order-basket">
          <div className="order-basket__title">В вашей корзине пусто</div>
        </div>
      );
    }

    return (
      <div className="order-process__basket order-basket">
        <div className="order-basket__title">В вашей корзине:</div>
        <div className="order-basket__item-list">

          {this.state.items.map((item) =>
            <OrderItem key={item.id} 
              item={item} cartInfo={this.getProductCartInfo(item.id)} />
          )}

        </div>
        <div className="order-basket__summ">
          Итого:&nbsp;
          <span>
            {formatBigNumbers(this.getTotalSum())} <i className="fa fa-rub" aria-hidden="true"></i>
          </span>
        </div>
      </div>
    );
  }
}

export default Cart;



