import React, { Component } from 'react';

import appConfig from 'appConfig';

import Cart from 'components/order/Cart';
import CartPanel from './CartPanel';

class CartFetcher extends Component {
  constructor(props) {
    super(props);

    this.state = {
      response: null,
      items: []
    };
  }

  componentWillMount() {
    this.fetchCart();
  }

  fetchCart = () => {
    if (!localStorage.getItem('cartId')) {
      return;
    }
    const cartId = localStorage.getItem('cartId') || '';
    
    const params = {
      method: 'GET',
      headers: new Headers({
        'Content-Type': 'application/json'
      })
    };
    
    fetch(`${appConfig.apiBaseUrl}/cart/${cartId}`, params)
      .then(response => response.json())
      .then(result => {
        this.setState({
          response: result
        });

        if (result.data && result.data.products) {
          this.fetchProducts();
        }
      });
  }

  fetchProducts() {
    let cartArrayPromises = [];
    this.state.response.data.products.forEach((product) => {
      cartArrayPromises.push(
        this.fetchProductInfo(product.id)
        .then(result => result.data)
      );
    });
  
    Promise.all(cartArrayPromises)
      .then(result => {
        this.setState({
          items: result
        });
      })
      .catch(error => {
        console.error(error);
      });
  }

  fetchProductInfo = (productId) => {
    const params = {
      method: 'GET',
      headers: new Headers({
        'Content-Type': 'application/json'
      })
    };
    
    return fetch(`${appConfig.apiBaseUrl}/products/${productId}`, params)
      .then(response => response.json())
      .then(result => result);
  }

  render() {
    if (this.props.type === 'panel') {
      return (
        <CartPanel {...this.state} {...this.props} data={this.state.response} items={this.state.items} />
      );
    } else if (this.props.type === 'cart') {
      return (
        <Cart {...this.state} {...this.props} data={this.state.response} items={this.state.items} />
      );
    }

  }
};

export default CartFetcher;



