import React, { Component } from 'react';

import appConfig from 'appConfig';

const withFetcher = ({url, params}) => WrappedComponent => class extends Component {
  constructor(props) {
    super(props);
    this.state = {
      response: null
    };
  }

  componentWillMount() {
    const params = {
      method: 'GET',
      headers: new Headers({
        'Content-Type': 'application/json'
      })
    };
    
    fetch(`${appConfig.apiBaseUrl}/${url}`, params)
      .then(response => response.json())
      .then(result => {
        this.setState({
          response: result
        });
      });
  }

  render() {
    return (
      <WrappedComponent {...this.state} {...this.props} data={this.state.response} />
    );
  }
};

export default withFetcher;



