export const formatProductsAmount = (amount) => {
  const amountFormattedString = formatBigNumbers(amount);

  const amountString = amount.toString();
  let type = '';

  if (amountString.length > 1 && amountString[amountString.length - 2] === '1') {
    type = 'товаров';
  } else {
    const lastSymbol = amountString[amountString.length - 1];

    if (['0', '5', '6', '7', '8', '9'].includes(lastSymbol)) {
      type = 'товаров';
    } else if (['2', '3', '4'].includes(lastSymbol)) {
      type = 'товара';
    } else if (lastSymbol === '1') {
      type = 'товар';
    }
  }

  return `${amountFormattedString} ${type}`;
}

export const formatBigNumbers = (value) => {
  const chunkLength = 3;
  const decimalDelimiter = ' ';

  if (value.toString().length < chunkLength) {
    return value.toString();
  }

  let integerPartArray = [];
  let integerPart = value.toString();
  do {
    integerPartArray.push(integerPart.substr(-1 * chunkLength));
    integerPart = integerPart.slice(0, integerPart.length - chunkLength);
  } while (integerPart.length > chunkLength);
  integerPartArray.push(integerPart);

  return integerPartArray.reverse().join(decimalDelimiter);
}



