import React, { Component } from 'react';

class NewProductsMenu extends Component {
  constructor(props) {
    super(props);

    this.state = {
      data: props.data,
      currentMenuItemIndex: 0
    };
  }

  componentWillReceiveProps(nextProps) {
		this.setState({
      data: nextProps.data
    });

    if (nextProps.data) {
      this.props.onSelectCategory(nextProps.data.data[this.state.currentMenuItemIndex].id);
    }
  }

  handleCategoryChange = (index, categoryId) => (event) => {
    this.setState({
      currentMenuItemIndex: index
    });
    this.props.onSelectCategory(categoryId);
  }

  render() {
    return (
      <div className="new-deals__menu">
        <ul className="new-deals__menu-items">
          {this.state.data && this.state.data.data.length > 0 ?
            this.state.data.data.map((item, index) =>
            <li key={item.id}
              className={"new-deals__menu-item " + (this.state.currentMenuItemIndex === index ? 'new-deals__menu-item_active' : '')}>
              <a 
                onClick={this.handleCategoryChange(index, item.id)}>
                {item.title}
              </a>
            </li> ) : ''}
        </ul>
      </div>
    );
  }
}

export default NewProductsMenu;



