import React, { Component } from 'react';
import appConfig from 'appConfig';

import Product from './Product';

class ProductWithData extends Component {
  constructor(props) {
    super(props);

    this.state = {
      productId: null,
      response: null
    };
  }

  componentWillMount() {
    this.fetchData();
  }

  componentWillUpdate(nextProps, nextState) {
    const { match: { params } } = this.props;

    if (params.productId !== this.state.productId) {
      this.fetchData();
    }
  }

  fetchData = () => {
    const { match: { params } } = this.props;

    const queryParams = {
      method: 'GET',
      headers: new Headers({
        'Content-Type': 'application/json'
      })
    };

    this.setState({
      productId: params.productId
    });

    fetch(`${appConfig.apiBaseUrl}/products/${params.productId}`, queryParams)
      .then(response => response.json())
      .then(result => {
        this.setState({
          response: result
        });
      });
  }

  render() {
    return (
      <Product {...this.state} {...this.props} data={this.state.response} />
    );
  }
};

export default ProductWithData;



