import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import Header from 'components/shared/Header';
import Footer from 'components/shared/Footer';
import OverlookedSlider from 'components/shared/OverlookedSlider';
import { checkIfFavorite, addToFavorite, removeFromFavorite } from 'components/favorite/favoriteFunctions';
import { formatBigNumbers } from 'components/utils/formatters';
import { addToCart } from 'components/order/cartFunctions';
import { getCategoryById } from 'components/shared/categoriesFunctions';

import 'css/style.css';
import 'css/style-order.css';
import 'css/style-product-card.css';
import SimilarProducts from './SimilarProducts';

class Product extends Component {
  constructor(props) {
    super(props);

    this.state = {
      data: props.data,
      currentSize: null,
      currentImageIndex: 0,
      isFavorite: null,
      currentAmount: 1,
      category: null
    };
  }

  componentWillReceiveProps(nextProps) {
		this.setState({
      data: nextProps.data,
      currentSize: nextProps.data.data.sizes[0],
      isFavorite: checkIfFavorite(nextProps.data.data.id)
    });

    getCategoryById(nextProps.data.data.categoryId)
      .then(result => {
        this.setState({
          category: result
        });
      });
  }

  chooseSize = (size) => (event) => {
    this.setState({
      currentSize: size
    });
  }

  goToPrevSlide = () => {
    if (this.state.currentImageIndex > 0) {
      this.setState({
        currentImageIndex: this.state.currentImageIndex - 1
      });
    }
  }
  goToNextSlide = () => {
    if (this.state.currentImageIndex + 2 < this.state.data.data.images.length - 1) {
      this.setState({
        currentImageIndex: this.state.currentImageIndex + 1
      });
    }
  }

  toggleFavorite = (productId) => (event) => {
    if (this.state.isFavorite) {
      removeFromFavorite(productId);
    } else {
      addToFavorite(productId);
    }

    this.setState({
      isFavorite: checkIfFavorite(this.props.data.data.id)
    });
  }

  decreaseAmount = () => {
    if (this.state.currentAmount > 0) {
      this.setState({
        currentAmount: this.state.currentAmount - 1
      });
    }
  }
  increaseAmount = () => {
    this.setState({
      currentAmount: this.state.currentAmount + 1
    });
  }

  addToCartWrapper = () => {
    addToCart(this.state.data.data.id, this.state.currentSize.size, this.state.currentAmount);
  }

  render() {
    if (!this.state.data) {
      return '';
    }
    
    const product = this.state.data.data;
  
    return (
      <div className="container">
        <Header />

        <div className="site-path">
          <ul className="site-path__items">
            <li className="site-path__item">
              <Link 
                to="/"
                className="site-path__item-link">
                Главная
              </Link>
            </li>
            <li className="site-path__item">
              <Link
                to={{
                  pathname: '/catalog',
                  search: `?categoryId=${product.categoryId}`
                }}
                className="site-path__item-link">
                {this.state.category}
              </Link>
            </li>
            <li className="site-path__item">
              <a className="site-path__item-link">
                {product.title}
              </a>
            </li>
          </ul>
        </div>

        <main className="product-card">
					<section className="product-card-content">
					  <h2 className="section-name">{product.title}</h2>
						<section className="product-card-content__main-screen">
									
							<section className="main-screen__favourite-product-slider">
								<div className="favourite-product-slider">
                  <div
                    onClick={this.goToPrevSlide} 
                    className="favourite-product-slider__arrow favourite-product-slider__arrow_up arrow-up">
                    <span className="visually-hidden">Предыдущее изображение</span>
                  </div>

                  {product.images[this.state.currentImageIndex] ? 
                    <div 
                      className="favourite-product-slider__item"
                      style={{backgroundImage: `url(${product.images[this.state.currentImageIndex]})`}}>
                      <a 
                        href={product.images[this.state.currentImageIndex]}
                        target="_blank">
                          <span className="visually-hidden">Изображение товара</span>
                        </a>
                    </div> : ''
                  }

                  {product.images[this.state.currentImageIndex + 1] ? 
                    <div 
                      className="favourite-product-slider__item"
                      style={{backgroundImage: `url(${product.images[this.state.currentImageIndex + 1]})`}}>
                      <a 
                        href={product.images[this.state.currentImageIndex + 1]}
                        target="_blank">
                          <span className="visually-hidden">Изображение товара</span>
                        </a>
                    </div> : ''
                  }

                  {product.images[this.state.currentImageIndex + 2] ? 
                    <div 
                      className="favourite-product-slider__item"
                      style={{backgroundImage: `url(${product.images[this.state.currentImageIndex + 2]})`}}>
                      <a 
                        href={product.images[this.state.currentImageIndex + 2]}
                        target="_blank">
                          <span className="visually-hidden">Изображение товара</span>
                        </a>
                    </div> : ''
                  }

                  <div
                    onClick={this.goToNextSlide} 
                    className="favourite-product-slider__arrow favourite-product-slider__arrow_down arrow-down">
                      <span className="visually-hidden">Следующее изображение</span>
                    </div>
		       	    </div>
							</section>


							<div className="main-screen__favourite-product-pic">
								<a href={product.images[0]} target="_blank">
                  <img src={product.images[0]} alt={product.title} />
                </a>
								<a className="main-screen__favourite-product-pic__zoom">
                  <span className="visually-hidden">Увеличить изображение</span>
                </a>
							</div>
							
							<div className="main-screen__product-info">
								<div className="product-info-title">
                  <h2>{product.title}</h2>
                  {this.state.currentSize.available ? 
                    <div className="in-stock">В наличии</div> : 
                    <div className="in-stock">Нет в наличии</div>}
                </div>
								<div className="product-features">
									<table className="features-table">
                    <tbody>
                      <tr>
                        <td className="left-col">Артикул:</td>
                        <td className="right-col">{product.sku}</td>
                      </tr>
                      <tr>
                        <td className="left-col">Производитель:</td>
                        <td className="right-col">
                          <a href="">
                            <span className="producer">{product.brand}</span>
                          </a>
                        </td>
                      </tr>
                      <tr>
                        <td className="left-col">Цвет:</td>
                        <td className="right-col">{product.color}</td>
                      </tr>
                      <tr>
                        <td className="left-col">Материалы:</td>
                        <td className="right-col">{product.material}</td>
                      </tr>
                      <tr>
                        <td className="left-col">Сезон:</td>
                        <td className="right-col">{product.season}</td>
                      </tr>
                      <tr>
                        <td className="left-col">Повод:</td>
                        <td className="right-col">{product.reason}</td>
                      </tr>
                    </tbody>
									</table>
								</div>
								
                <p className="size">Размер</p>
								<ul className="sizes">
                  {product.sizes.map((size, index) => 
                    <li key={`${size}${index}`} 
                      className={this.state.currentSize === size ? 'active' : ''}>
                      <a onClick={this.chooseSize(size)}>{size.size}</a>
                    </li>
                  )}
                </ul>
								<div className="size-wrapper">
                  <a href="">
                    <span className="size-rule"></span>
                    <p className="size-table">Таблица размеров</p>
                  </a>
								</div>
                <a 
                  onClick={this.toggleFavorite(product.id)}
                  className="in-favourites-wrapper">
                  <div 
                    className={"favourite " + (this.state.isFavorite ? 'favourite--active' : '')}></div>
                  {this.state.isFavorite ? 
                    <p className="in-favourites">В избранном</p> : 
                    <p className="in-favourites">В избранное</p>
                  }
								</a>

								<div className="basket-item__quantity">
                  <div
                    onClick={this.decreaseAmount} 
                    className="basket-item__quantity-change basket-item-list__quantity-change_minus">-</div>
                  {this.state.currentAmount}
                  <div
                    onClick={this.increaseAmount} 
                    className="basket-item__quantity-change basket-item-list__quantity-change_plus">+</div>
								</div>

                <div className="price">
                  {formatBigNumbers(product.price)} ₽
                </div>
                {this.state.currentSize.available && 
                  this.state.currentAmount > 0 ? 
                  <button
                    onClick={this.addToCartWrapper} 
                    className="in-basket in-basket-click">
                    В корзину
                  </button> : ''}
							</div>

						</section>
					</section>
        </main>

        <OverlookedSlider />

        <SimilarProducts />

        <Footer />
      </div>
    );
  }
}

export default Product;



