import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import Header from 'components/shared/Header';
import Footer from 'components/shared/Footer';
import CartFetcher from 'components/shared/CartFetcher';

import 'css/style.css';
import 'css/style-order.css';
import OrderForm from './OrderForm';
import OrderDone from './OrderDone';

class Order extends Component {
  constructor(props) {
    super(props);

    this.state = {
      orderData: null
    };
  }

  showOrderData = (data) => {
    this.setState({
      orderData: data
    });
  }

  render() {
    return (
      <div className="container">
        <Header />

        <div className="wrapper order-wrapper">               
          <div className="site-path">
            <ul className="site-path__items">
              <li className="site-path__item">
                <Link 
                  to="/"
                  className="site-path__item-link">
                  Главная
                </Link>
              </li>
              <li className="site-path__item">
                <a className="site-path__item-link">Корзина</a>
              </li>
              <li className="site-path__item">
                <a className="site-path__item-link">Оформление заказа</a>
              </li>
            </ul>
          </div>

          {!this.state.orderData ? 
            <section className="order-process">
              <h2 className="order-process__title">Оформление заказа</h2>
              
              <CartFetcher type="cart" />

              <OrderForm onSuccess={this.showOrderData} />

            </section> : 
            <OrderDone data={this.state.orderData} />
          }
        </div>

        <Footer />
      </div>
    );
  }
}

export default Order;



