import React, { Component } from 'react';

import DroppedMenu from './DroppedMenu';

class MainMenu extends Component {
  constructor(props) {
    super(props);

    this.state = {
      data: props.data,
      currentCategoryId: null
    };
  }

  componentWillReceiveProps(nextProps) {
		this.setState({
      data: nextProps.data
    });  
  }

  toggleSecondaryMenu = (categoryId) => (event) => {
    if (this.state.currentCategoryId === null) {
      this.setState({
        currentCategoryId: categoryId
      });
    } else {
      this.setState({
        currentCategoryId: null
      });
    }
  }

  render() {
    return (
      <React.Fragment>
        <nav className="main-menu">
          <div className="wrapper">
            <ul className="main-menu__items">

              {this.state.data && this.state.data.data.length > 0 ? 
                this.state.data.data.map((item) =>
                <li 
                  key={item.id}
                  className={"main-menu__item " + (this.state.currentCategoryId === item.id ? 'main-menu__item_active' : '')}>
                  <a 
                    onClick={this.toggleSecondaryMenu(item.id)}
                    className="main-menu__item-link">
                    {item.title}
                  </a>
                </li> ) : ''}

            </ul>
          </div>
        </nav>

        {this.state.currentCategoryId !== null ? 
          <DroppedMenu 
            categoryId={this.state.currentCategoryId}
            hideMenu={this.toggleSecondaryMenu()} />
          : ''}
      </React.Fragment>
    );
  }
}

export default MainMenu;



